@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #fff;
}
nav {
  border-bottom: 1px solid var(--cardborder);
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  text-transform: capitalize;
  background: transparent !important;
  z-index: 999999;
  padding: 30px;
  color: var(--navtext);
}
nav a,
.logo {
  color: var(--text-color);
  font-weight: 400;
  font-family: "Montserrat";
  color: var(--navtext);
}

.logo .for_mobile {
  display: none;
}
.mainHeadingNav {
  font-size: 18px;
}
.mainHeadingNav p span {
  font-weight: 700;
}
nav ul {
  display: flex;
  justify-content: space-around;
  width: 40%;
}
ul li {
  cursor: pointer;
  color: var(--navtext);
}
nav li a .icons {
  color: #fff;
  display: none;
  font-size: 20px;
}
li a span {
  transition: 0.2s ease;
  color: var(--navtext);
}
li a span:hover {
  color: var(--button);
}

nav ul li p {
  color: #fff;
  font-size: 10px;
  margin-top: 5px;
  display: none;
  letter-spacing: 0.5px;
  margin-right: 3px;
}

/* ----------- design for Dropdown  search ------------ */
.dropdown_data {
  z-index: 100;
  position: absolute;
  top: 45px;
  right: 100px;
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #343a40;
  font-family: "poppins", sans-serif;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
  transition: 0.2s ease-in-out;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
}

/* ---------after for the triangle------------- */

.li {
  font-size: 12px;
  padding: 15px 10px;
  border-radius: 5px;
  display: block;
}
.drp_down_holder {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.dropdown_data .li:hover {
  background-color: var(--feelhover);
}

.user__login {
  display: flex;
}
.user__login .li {
  margin: 0 5px;
}
.logedin_data {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 20px;
}
/*//notif*/
.notif_btn {
  margin-left: 20px;
}
.notif_btn i {
  font-size: 18px;
  color: var(--headingcolor);
  /*background: var(--button);*/
  padding: 8px;
  border-radius: 50%;
}
.notif_len {
  font-size: 60px;
  color: var(--headingcolor);
}

/* ------------ responsive code for nav bar starts ------------- */
@media (max-width: 1200px) {
  .dropdown_data {
    right: 60px;
  }
}
@media (max-width: 768px) {
  .mainHeadingNav {
    color: #fff !important;
  }

  .dropdown_data {
    top: 36px;
  }
  nav {
    padding: 0px;
  }
  .special {
    display: none !important;
  }
  .for_desktop {
    display: none;
  }
  .for_mobile {
    display: block !important;
    padding-right: 20px;
  }
  .for_mobile i {
    color: #fff;
  }
  .special_links {
    display: block !important;
  }
  .fa-user-circle {
    font-size: 30px !important;
  }
  nav .logo {
    width: 100%;
    height: 50px;
    background-color: #343a40;
    color: #fff !important;
    padding-left: 20px;
    position: fixed;
    font-size: 15px;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    font-size: 25px;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
  }

  nav ul {
    position: fixed;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    background-color: #343a40;

    height: 50px;
    display: flex;
    align-items: center;
    padding: 5px 0;
    z-index: 999;
  }
  nav li a .icons {
    color: #fff;
    display: block;
  }
  nav li a:hover .icons {
    color: var(--button);
  }
  a span {
    display: none;
  }
  section h2 {
    padding: 0 10px;
  }

  nav .indi {
    display: block;
  }

  .dropdown_data {
    right: 20px;
  }
  nav .logo {
    color: #fff !important;
  }
}
.nav_active {
  border-bottom: 3px solid var(--button);
}

.nav_active #ac_icon,
.nav_active .indi {
  color: var(--button) !important;
}
.li_pro {
  /*padding: 10px 0;*/
  /*display: block;*/
}
.con_link_pro {
  display: flex;
  /*margin-bottom: 10px;*/
}
.right_link_pro {
  padding-left: 10px;
}
.right_link_pro p {
  font-size: 15px;
}
.left_link_pro img {
  border-radius: 50%;
}
.btn__viewPro {
  width: 100%;
  border: 1px solid grey;
  padding: 0px 5px;
  border-radius: 20px;
  text-align: center;
  margin-bottom: 5px;
}
.btn__viewPro:hover {
  border: 1px solid var(--button);
}
.pro__link {
  font-size: 14px;
}
.btn__viewPro {
  margin-top: 5px;
}
.light_upload {
  color: rgba(255, 255, 255, 0.637);
  font-weight: 200;
  letter-spacing: 1px;
  font-size: 12px !important;
}

.lifor {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
}
footer {
  width: 100%;
  margin-top: auto;
  background: var(--footerbg);
  border-top: 1px solid var(--cardborder);
}
footer .content {
  max-width: 1350px;
  margin: auto;
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
footer > * {
  font-size: 13px;
}
footer .content p,
footer a {
  color: var(--footertxt);
}

footer .content .box {
  width: 33%;
  /*transition: all 0.4s ease;*/
}
footer .content .topic {
  font-size: 18px;
  color: var(--footertxt);
  margin-bottom: 16px;
}
.weather {
  margin-top: 20px;
}
.weather .weatherheading {
  font-size: 18px;
  margin-bottom: 5px;
}
footer .content p {
  text-align: left;
}
footer .content .lower .topic {
  margin: 24px 0 5px 0;
}
footer .content .lower i {
  padding-right: 16px;
}
footer .content .middle {
  padding-left: 80px;
}
footer .content .middle a {
  line-height: 32px;
}

footer .content .media-icons a {
  font-size: 16px;
  height: 45px;
  width: 45px;
  display: inline-block;
  text-align: center;
  line-height: 43px;
  border-radius: 5px;
  border: 2px solid #222222;
  margin: 30px 5px 0 0;
}
/*ahah */

footer .bottom {
  width: 100%;
  text-align: right;
  color: var(--footertxt);
  padding: 0 40px 5px 0;
}
.bottom p {
  color: var(--footertxt);
}
.cncttxt {
  color: var(--footertxt);
}
footer .content .right textarea {
  height: 80px;
  width: 100%;
  outline: none;
  color: var(--textareacolor);
  background: var(--footertxtarea);
  border-radius: 5px;
  padding-left: 10px;
  font-size: 17px;
  border: 2px solid #222222;
  resize: none;
}
footer .content .right button {
  height: 42px;
  width: 100%;
  font-size: 18px;
  color: var(--btntext);
  background: var(--button);
  outline: none;
  border: 0;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.disabled {
  cursor: not-allowed;
  background: grey;
}

@media (max-width: 1100px) {
  footer .content .middle {
    padding-left: 50px;
  }
}
@media (max-width: 950px) {
  footer .content .box {
    width: 50%;
  }
  .content .right {
    margin-top: 40px;
  }
  footer {
    padding-bottom: 50px;
  }
}
@media (max-width: 560px) {
  footer {
    position: relative;
  }
  footer .content .box {
    width: 100%;
    margin-top: 30px;
  }
  footer .content .middle {
    padding-left: 0;
  }

  footer .bottom {
    text-align: left;
    padding: 0 40px 5px 25px;
  }
}

.mainLoader {
  min-height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.521);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 99999;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

